<template>
  <div id="app">
    <div class="stopEditor" @click.once="stop()"></div>
    <StyleEditor ref="styleEditor" :code="currentStyle"></StyleEditor>
    <ResumeEditor ref="resumeEditor" :markdown="currentMarkdown" :enableHtml="enableHtml"></ResumeEditor>
  </div>
</template>

<script>
  import StyleEditor from './components/StyleEditor'
  import ResumeEditor from './components/ResumeEditor'
  import './assets/reset.css'

  export default {
    name: 'app',
    components: {
      StyleEditor,
      ResumeEditor
    },
    data() {
      return {
        interval: 30,
        currentStyle: '',
        enableHtml: false,
        fullStyle: [
          `/*
 * Hi 
 *  ()
 * 
 * 
 *  3 
 *  2 
 *  1 
*/

/*  */
* {
  transition: all .3s;
}

/*  */
html {
  color: rgb(53,53,53);
  background: rgb(232,233,237);
}

/*  */
.styleEditor {
  margin: 1em;
  padding: 1em;
  width: 30vw;
  height: 95vh;
  background: rgb(242,242,242);
  border-radius:3px;
  overflow: auto;
}

/*  */
.token.selector{color: rgb(133,153,0 );}
.token.property{color: rgb(187,137,0 );}
.token.punctuation{color: black;}
.token.function{color :rgb(42,161,152);}

/*  3D  */
html{
  perspective: 1000px;
}
.styleEditor {
  position: fixed; left: 0; top: 0;
  -webkit-transition: none;
  transition: none;
  -webkit-transform: rotateY(10deg)
                     translateZ(-100px);
          transform: rotateY(10deg)
                     translateZ(-100px);
}

/* ...3D*/
.styleEditor{
  transform:none;
}

/* 
 * 
 * ,~~~
 * HaHa
 */

/*  */
.resumeEditor{
  font-family: "Georgia", "Xin Gothic",
        "Hiragino Sans GB", "Droid Sans Fallback",
        "Microsoft YaHei", sans-serif;
  position: fixed;
  right: 0; top: 0;
  padding: 1em;  margin: 1em;
  width: 65vw; height: 95vh;
  border-radius: 5px;
  background: rgb(211,217,227);
  color: #222;
  overflow: auto;
}

/* OK */
`,
          `
/* ...
 * emm Markdown 
 * 
 *  HTML 
 */
`,
          `
/*  HTML  */
.resumeEditor{
  padding: 2em;
}
.resumeEditor p{
  margin: 10px 0;
  color: #555;
}
.resumeEditor h1{
  font-size: 1.6rem;
  line-height: 28px;
  margin: 52px 0 15px 0;
  padding-bottom: 7px;
  color: #000;
}
.resumeEditor h2{
  font-size: 1.3rem;
  line-height: 23px;
  margin: 30px 0 13px 0;
  padding-bottom: 7px;
  color: #333;
}
.resumeEditor h3{
  font-size: 1.2rem;
  line-height: 18px;
  margin: 20px 0 2px;
  padding-bottom: 7px;
  color: #333;
}
.resumeEditor ul{
  display: block;
  list-style-type: disc;
  padding-left: 17px;
  margin: 6px 0 10px;
  color: #555;
}
.resumeEditor ul li{
  position: relative;
  left: 15px;
}
.resumeEditor blockquote {
  border-left: 4px solid #aba8a8;
  padding: 0 15px;
}
.resumeEditor blockquote>:last-child {
  margin-bottom: 15px;
}
.resumeEditor blockquote>:first-child {
  margin-top: 15px;
}
`],
        currentMarkdown: '',
        fullMarkdown: `
## 
- //1995
- /
- Java
- 13323007604
- Emaildpw0609@163.comdupingwei@qq.com
- QQ775357468

## 

- JavaJavaSOANode
- SpringSpringMVCMybatisStruts2HibernateJFinalExpress
- UEditorDUBBOzookeeper
- HTMLAjaxJQueryBootstrapVueSassWebpack
- MySQLOracleSQLNoSQLRedis
- SVNGit
- LinuxJava

## 

###  2017/01 -- 2017/05

#### IStorm V3.2 
> Eclipsetomcat8MySQL5.6SpringMyBatisSpringMVC
>
> B/S
>
> 

###  2017/07 -- 2018/03

#### 
> WebStormNodevueiView
>
> vue-cliiViewUI vuexvue-amapvue-xlsx-tableaxiossassnode.js,requestexpersssuperagentcrypto
>
> NodejMeter

#### 
> 
>
> bug

#### 
  > ActiveMQ
  >
  > : OracleMySQL

## 
- 
- 
- 
-

`
      }
    },
    created() {
      this.makeResume()
    },

    methods: {
      makeResume: async function () {
        await this.progressivelyShowStyle(0);
        await this.progressivelyShowResume();
        await this.progressivelyShowStyle(1);
        await this.showHtml();
        await this.progressivelyShowStyle(2)
      },
      showHtml: function () {
        return new Promise((resolve, reject) => {
          this.enableHtml = true;
          resolve()
        })
      },
      progressivelyShowStyle(n) {
        return new Promise((resolve, reject) => {
          let interval = this.interval;
          let showStyle = (async function () {
            let style = this.fullStyle[n];
            if (!style) {
              return
            }
            //  n  style 
            let length = this.fullStyle.filter((_, index) => index <= n).map((item) => item.length).reduce((p, c) => p + c, 0)
            let prefixLength = length - style.length;
            if (this.currentStyle.length < length) {
              let l = this.currentStyle.length - prefixLength;
              let char = style.substring(l, l + 1) || ' ';
              this.currentStyle += char;
              if (style.substring(l - 1, l) === '\n' && this.$refs.styleEditor) {
                this.$nextTick(() => {
                  this.$refs.styleEditor.goBottom();
                })
              }
              setTimeout(showStyle, interval)
            } else {
              resolve()
            }
          }).bind(this);
          showStyle()
        })
      },
      progressivelyShowResume() {
        return new Promise((resolve, reject) => {
          let length = this.fullMarkdown.length;
          let interval = this.interval;
          let showResume = () => {
            if (this.currentMarkdown.length < length) {
              this.currentMarkdown = this.fullMarkdown.substring(0, this.currentMarkdown.length + 1);
              let lastChar = this.currentMarkdown[this.currentMarkdown.length - 1];
              let prevChar = this.currentMarkdown[this.currentMarkdown.length - 2];
              if (prevChar === '\n' && this.$refs.resumeEditor) {
                this.$nextTick(() => this.$refs.resumeEditor.goBottom())
              }
              setTimeout(showResume, interval)
            } else {
              resolve()
            }
          };
          showResume()
        })
      },
      stop() {
        this.currentMarkdown = this.fullMarkdown;
        this.currentStyle = this.fullStyle;
      }
    }
  }

</script>

<style scoped>
  #app {
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  html {
    min-height: 100vh;
  }

  * {
    box-sizing: border-box;
  }

  .stopEditor {
    width: 50px;
    height: 50px;
    position: fixed;
    right: 50px;
    top: 35px;
    z-index: 1040;
    cursor: pointer;
    background-color: rgba(107,138,167,0.7);
    border-radius: 50%;
  }
</style>



